import axios from 'axios';

interface Train {
  id: number;
  trainName: string;
  departCity: string;
  arrivalCity: string;
  departTime: string;
  arrivalTime: string;
  duration: string;
  trainNumber?: string;
}

export async function getServerSideProps() {
  try {
    const response = await axios.get<Train[]>('http://localhost:3000/trains');
    return {
      props: {
        trains: response.data,
      },
    };
  } catch (error) {
    console.error('Помилка при завантаженні поїздів:', error);
    return {
      props: {
        trains: [],
      },
    };
  }
}

export default function UserPage({ trains }: { trains: Train[] }) {
  return (
    <div className="p-6 bg-gray-100 min-h-screen">
      <h1 className="text-3xl font-bold mb-6 text-gray-800">Розклад поїздів</h1>
      {trains.length === 0 ? (
        <p className="text-gray-500">Поїздів не знайдено.</p>
      ) : (
        <ul className="space-y-4">
          {trains.map((train) => (
            <li
              key={train.id}
              className="p-4 bg-white border rounded-lg shadow-sm"
            >
              {train.trainName} | {train.departCity} → {train.arrivalCity} | 
              Час відправлення: {new Date(train.departTime).toLocaleString()} | 
              Час прибуття: {new Date(train.arrivalTime).toLocaleString()} | 
              Тривалість: {train.duration} | 
              Номер поїзда: {train.trainNumber || 'Немає'}
            </li>
          ))}
        </ul>
      )}
    </div>
  );
}